<template>

<div>
	<div class="search-wrapper " v-bind:class="{focus:focus}">
		<div v-on:click="mask"  class="mask " v-bind:class="{hide:!focus}"></div>
		<div class="search">
			<div class="input">
				<input type="search" v-el:search required="" v-model="value" name="">
				<span v-on:click="search"  class="placeholder" >
					<i class="paco icon-search" ></i>
					<span class="text"></span>
				</span>
				<i v-on:click="mask" class="clear paco icon-wrong"></i>
			</div>
			<span class="cancel" v-on:click="mask"></span>
		</div>
	</div>	
</div>
</template>
<script>
	export default{
		name:'paco-search',
		props:{
			value:{type:String}
		},
		data(){
			return{
				focus:false
			}
		},
		methods:{
			search () {
				this.$els.search.focus();
				this.focus=true
				
			},
			mask () {
				this.value=''
				this.focus=false
			}
		}
	}
</script>


