<template>
	<div class="input-box">
		<label class="label " v-bind:class="{'hide':label}"><slot></slot></label>
		<input class="input" v-bind:placeholder="til" v-model="value">
		<i class="paco icon" v-bind:class="{'icon-wrong':wrong,'icon-camera':camera,'highlight':highlight}" ></i>
		<span v-on:click="times()" v-if="show"  v-bind:class="{'hide':!span,'verification':span}" >{{check}}</span>
		<span v-else v-bind:class="{'hide':!span,'verification':span}"  class="timing">{{check}}</span>
	</div>
</template>
<script >
	export default{
		name:'Inputs',
		props:{
			label:{type:[Boolean,String],default:false},
			span:{type:[Boolean,String],default:false},
			wrong:{type:[Boolean,String],default:false},
			camera:{type:[Boolean,String],default:false},
			highlight:{type:[Boolean,String],default:false},
			til:{type:String},
			value:{type:String}
		},
		data(){
			return{
				check:'',
				show:true
			}
		},
		methods:{
			times(){
				let s=60
				let t=setInterval(()=>{
					if(s>1){
						s--
						this.check=s+""
						this.show=false
					}else{
						window.clearInterval(t);
						this.check=""
						this.show=true
						
					}
				},1000);
			}

		}

	}
</script>




